import Axios from 'axios'; // axios
import { Notification } from 'element-ui';

const axios = Axios.create({
    baseURL: process.env.VUE_APP_BASEURL || '',
    timeout: 30000
});

// Promise
// window.addEventListener('unhandledrejection', function(e) {
//     console.error(e);
// });

// 
axios.interceptors.request.use(
    function(config) {
        // storetoken
        // config.headers['Authorization'] = `Bearer ${store.getters.getAccessToken}`
        if (config.method.toLocaleLowerCase() === 'post' || config.method.toLocaleLowerCase() === 'put') {
            // data
            config.data = config.data.data;
        } else if (config.method.toLocaleLowerCase() === 'get' || config.method.toLocaleLowerCase() === 'delete') {
            // 
            config.params = config.data;
        } else {
            alert('' + config.method);
        }
        return config;
    },
    function(error) {
        // 
        return Promise.reject(error);
    }
);

// 
axios.interceptors.response.use(
    function(response) {
        // 
        if (response.data === 'Not Found') {
            // Not Found
            Notification({
                title: response.config.url,
                message: '',
                type: 'error'
            });
        } else if (response.status == 200 || response.status == 304) {
            // {code: xxx, data: xxx, msg:'err message'}
            // code:200  200 
            if (response.data.code == 200) {
                // if (response.config.method.toLocaleLowerCase() === 'post' || response.config.method.toLocaleLowerCase() === 'put') {
                //     Notification({
                //         title: '',
                //         message: response.data.msg,
                //         type: 'success'
                //     });
                // }
                return response.data.data;
            } else {
                Notification({
                    title: '',
                    message: response.data.msg,
                    type: 'error'
                });
                return Promise.reject(response.data);
            }
        } else {
            Notification({
                title: response.config.url,
                message: '',
                type: 'error'
            });
        }
    },
    function(error) {
        Notification({
            title: '',
            message: error.message,
            type: 'error'
        });
        return Promise.reject(error);
    }
);
export default axios;
